/**
 * 获取当前时间 格式：yyyy-MM-dd HH:MM:SS
 */

function getCurrentTime() {
  let date = new Date(); //当前时间
  let month = zeroFill(date.getMonth() + 1); //月
  let day = zeroFill(date.getDate()); //日
  let hour = zeroFill(date.getHours()); //时
  let minute = zeroFill(date.getMinutes()); //分
  let second = zeroFill(date.getSeconds()); //秒

  //当前时间
  let curTime =
    date.getFullYear() +
    "-" +
    month +
    "-" +
    day +
    " " +
    hour +
    ":" +
    minute +
    ":" +
    second;

  return curTime;
}

/**
 * 补零
 */
function zeroFill(i) {
  if (i >= 0 && i <= 9) {
    return "0" + i;
  } else {
    return i;
  }
}

function splitData(rawData) {
  let categoryData = [];
  let values = [];
  let volumes = [];
  for (let i = 0; i < rawData.length; i++) {
    categoryData.push(rawData[i].splice(0, 1)[0]);
    values.push(rawData[i]);
    volumes.push([i, rawData[i][4], rawData[i][0] > rawData[i][1] ? 1 : -1]);
  }
  return {
    categoryData: categoryData,
    values: values,
    volumes: volumes
  };
}
function calculateMA(dayCount, data) {
  var result = [];
  for (var i = 0, len = data.values.length; i < len; i++) {
    if (i < dayCount) {
      result.push('-');
      continue;
    }
    var sum = 0;
    for (var j = 0; j < dayCount; j++) {
      sum += data.values[i - j][1];
    }
    result.push(+(sum / dayCount).toFixed(3));
  }
  console.log("ma:" + result);
  return result;
}
function initHeaders(headers,obj,exclude=[],name ="headers"){
  let length1=obj[name].length;
  let length2 = headers.length;
  for(let i =0;i<length1;i++)
  {
    obj[name].pop();
  }
  for(let i =0;i<length2;i++)
  {
    if(exclude.includes(headers[i]) &&exclude.length>0)
    {
      obj[name].push({text: "", align: "center",value: headers[i]});
    }
    else {
      obj[name].push({text: headers[i], align: "center", value: headers[i]});
    }
  }
}
