function ObjToArray(data,headers){
  let res=[]
  for(let header of headers){
    res.push(data[header])
  }
  return res
}

function cleanMeta(headers){
  let Meta = ["stock_id","_time","id","Unnamed: 0"]
  let res = []
  for(let header of headers){
    if(!Meta.includes(header)){
      res.push(header)
    }
  }
  return res;
}

function cleanFactor(metaStock){
  for (let key in metaStock) {
    if (
        key.includes("_") ||
        key === "id" ||
        key === "股票名称" ||
        key === "股票代码" ||
        key === "季度" ||
        key === "当前股价" ||
        key === "Unnamed: 0"||
        key === "中文名称" ||
        key === "当前股价(元)"
    ) {
      delete metaStock[key];
    }
  }
}
function reMapData(data,headers){
  for (let i = 0; i < data.length; i++) {
    let j = 0;
    let length = 0;
    if(Array.isArray(data) && data.length)
    {
      length=Object.keys(data[0]).length;
    }
    while (j <= length) {
      let key_text = Object.keys(data[i])[j];
      let key_index = headers.indexOf(key_text);
      let data_key = "headers" + key_index.toString();
      data[i][data_key] = data[i][key_text];
      j++;
    }
  }
}


module.exports = {
  splitData, initHeaders,ObjToArray,cleanMeta,initHeaders2,reMapData,cleanFactor
};
